.carousel-nevigation {
  position: absolute;
  top: 50%;
  float: left;
  width: 100%;
}
.carousel-nevigation > .prev {
  left: -138px;
  position: absolute;
  width: 50px;
  height: 50px;
  line-height: 50px;
  padding-top: 9px;
  padding-left: 3px;
  border-radius: 100%;
  background: #2c2e53;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.carousel-nevigation > .next {
  right: -138px;
  position: absolute;
  width: 50px;
  height: 50px;
  padding-top: 9px;
  padding-left: 12px;
  border-radius: 100%;
  background: #2c2e53;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.slider-area:hover .carousel-nevigation > .prev {
  left: 10px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.slider-area:hover .carousel-nevigation > .next {
  right: 10px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.carousel-nevigation > .next:hover,
.carousel-nevigation > .prev:hover {
  background: rgba(57, 100, 208, 1);
  background: -moz-linear-gradient(
    left,
    rgba(57, 100, 208, 1) 0%,
    rgba(45, 193, 201, 1) 100%
  );
  background: -webkit-gradient(
    left top,
    right top,
    color-stop(0%, rgba(57, 100, 208, 1)),
    color-stop(100%, rgba(45, 193, 201, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(57, 100, 208, 1) 0%,
    rgba(45, 193, 201, 1) 100%
  );
  background: -o-linear-gradient(
    left,
    rgba(57, 100, 208, 1) 0%,
    rgba(45, 193, 201, 1) 100%
  );
  background: -ms-linear-gradient(
    left,
    rgba(57, 100, 208, 1) 0%,
    rgba(45, 193, 201, 1) 100%
  );
  background: linear-gradient(
    to right,
    rgba(57, 100, 208, 1) 0%,
    rgba(45, 193, 201, 1) 100%
  );
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.carousel-nevigation > .prev i {
  float: left;
  width: 35px;
  text-align: right;
  color: #ffffff;
  font-size: 16px;
  font-size: 28px;
  padding-right: 10px;
}
.carousel-nevigation > .prev span {
  width: calc(100% - 35px);
  float: left;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  line-height: 17px;
}
.carousel-nevigation > .next i {
  float: left;
  width: 35px;
  text-align: left;
  color: #ffffff;
  font-size: 16px;
  font-size: 28px;
  padding-left: 10px;
}
.carousel-nevigation > .next span {
  width: calc(100% - 35px);
  float: left;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  line-height: 17px;
  text-align: right;
}
