/* display property style */
.flex-row-center {
	display: flex;
	align-items: center;
}
.flex-row-space {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.flex-row-end {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
}
.flex-column-center {
	display: flex;
	flex-direction: column;
}
.flex-center-center {
	display: flex;
	justify-content: center;
	align-items: center;
}
.flex-center {
	display: flex;
	align-items: center;
	justify-content: center;
}

/* text align */
.center {
	text-align: center;
}
.right {
	text-align: right;
}
.left {
	text-align: left;
}

/* modal */
.custom-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10;
}

.custom-modal .modal-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(17, 4, 16, 0.4);
}
.custom-modal .modal-bg .modal-layout {
	background: #ffffff;
	box-shadow: 4px 4px 10px 15px rgba(115, 115, 115, 0.02);
	border-radius: 20px;
	z-index: 2;
	position: relative;
}

/* width */
::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
	background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: rgba(30, 42, 70, 0.38);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgba(30, 42, 70, 0.5);
}
